import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from './pages/Home';
import CreateTeam from './pages/CreateTeam';
import TeamFight from './pages/TeamFight';
import TeamsList from './pages/TeamsList';
import ShowTeam from './pages/ShowTeam';
import Header from './components/Header';
import { AppContainer } from './styles/styledComponents';

function App() {
  return (
    <>
      <Header />
      <AppContainer>
        <Switch>
          <Route exact path="/football-app-v3/" render={() => <Home />} />
          <Route
            exact
            path="/football-app-v3/create"
            render={() => <CreateTeam />}
          />
          <Route
            exact
            path="/football-app-v3/fight"
            render={() => <TeamFight />}
          />
          <Route
            exact
            path="/football-app-v3/team/:id"
            render={(routeProps) => <ShowTeam {...routeProps} />}
          />
          <Route
            exact
            path="/football-app-v3/list"
            render={() => <TeamsList />}
          />
        </Switch>
      </AppContainer>
    </>
  );
}

export default App;
